.dashboard-table.ReactTable {
	max-height: 100%;
	background: white;
	border: 1px solid  rgba(92, 92, 92, 0.6);
	border-radius: 6px;
	box-shadow: 0 0 8px rgba(92, 92, 92, 0.5);
	margin-bottom: 15px;
	font-family: Open Sans Condensed;
	font-weight: 800;
	-webkit-animation: slide-in-right 0.7s;
	        animation: slide-in-right 0.7s;
	width: 100%;
	max-width: calc(100% - 42px);
	float: left;
}

.dashboard-table.ReactTable .rt-noData {
	color: rgba(0, 0, 0, 0.75);
}

.dashboard-table .rt-th {
	color: darkslategrey;
}

.dashboard-table .rt-thead {
	overflow-y: scroll;
	overflow-x: hidden;
}

.dashboard-table .rt-thead::-webkit-scrollbar {
	background: transparent;
}

.dashboard-table .rt-tbody {
	overflow-x: hidden !important;
}

.dashboard-table .rt-td {
	font-weight: 400;
}

/**
 * ----------------------------------------
 * animation slide-in-right
 * ----------------------------------------
 */
@-webkit-keyframes slide-in-right {
	0% {
		-webkit-transform: translateX(1000px);
		transform: translateX(1000px);
		opacity: 0;
	}

	100% {
		-webkit-transform: translateX(0);
		transform: translateX(0);
		opacity: 1;
	}
}

@keyframes slide-in-right {
	0% {
		-webkit-transform: translateX(1000px);
		transform: translateX(1000px);
		opacity: 0;
	}

	100% {
		-webkit-transform: translateX(0);
		transform: translateX(0);
		opacity: 1;
	}
}

.message-container {
	position: relative;
	width: 96%;
	height: auto;
	border-radius: 12px;
	padding: 10px;
	margin-bottom: 14px;
	top: 16px;
	background: white;
	color: #2e4468;
	box-shadow: 0 0 2px 1px darkgray;
	transition: all 0.3s;
}

.message-container:hover {
	background: white;
	color: red;
}

.message-data {
	width: 100%;
	margin-bottom: 4px;
	height: 48px;
}

.message-profile-pic {
	float: left;
	width: 48px;
	height: 48px;
	margin-right: 8px;
}

.message-profile-pic img {
	width: 100%;
	height: 100%;
	border-radius: 8px;
}

.message-author-line {
	font-family: Open Sans Condensed;
	float: left;
	font-size: 16px;
	height: 48px;
	margin-left: 6px;
	padding-top: 12px;
	color: #2e4468;
}

.message-author {
	color: #2e4468;
}

.message-author:hover {
	color: red;
}

.message-title {
	width: 100%;
	font-size: 22px;
	background: gainsboro;
	font-family: Open Sans Condensed;
	margin-top: 8px;
	margin-bottom: 8px;
	border-radius: 8px;
	padding-left: 6px;
}

.message-body {
	margin-bottom: 5px;
	color: darkslategray;
}

